.container {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.input-container {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
}
.reaction {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
}
.reaction-content {
    margin-right: 10px;
}
.delete-button {
    background-color: red;
    color: white;
    border: none;
    padding: 5px;
    cursor: pointer;
}
#pickerContainer {
    position: absolute;
    z-index: 1000;
}
